import React, { useEffect, useState, useRef } from "react";
import PhotoSwipeLightbox from "photoswipe/lightbox";
import { graphql } from "gatsby";
import { allSanityTypeItems } from "../utils/utils";
import Layout from "../components/layout/layout.component";
import Seo from "../components/seo/seo.component";
import PhotoCard from "../components/cards/photo-card/photo-card.component";
import Masonry from "masonry-layout";
import imagesloaded from "imagesloaded";
import "photoswipe/style.css";

const PhotographyPage = ({ data }) => {
  const ref = useRef();
  const [loaded, setLoaded] = useState(false);

  // console.log("isLoaded", loaded);

  useEffect(() => {
    if (typeof Masonry === "undefined" && !!ref.current) {
      console.log("ERROR: Masonry is not available. Fallback to bootstrap layout");
      ref.current.classList.add("row", "justify-content-center");
      return;
    }

    const masonry = new Masonry(".gallery", {
      itemSelector: ".grid-item", // The grid item selector
      columnWidth: ".grid-item", // The column width selector
      gutter: 15, // Space between grid items
      horizontalOrder: true, // Enable horizontal ordering
      fitWidth: true,
    });

    const imgLoad = imagesloaded(ref.current);

    imgLoad.on("always", () => {
      masonry.layout();
      setLoaded(true);
    });
    // Cleanup function
    return () => {
      imgLoad.off("done");
      masonry.destroy();
    };
  }, []);

  useEffect(() => {
    let lightbox = new PhotoSwipeLightbox({
      gallery: "#gallery",
      children: "a",
      pswpModule: () => import("photoswipe"),
    });
    lightbox.init();

    return () => {
      lightbox.destroy();
      lightbox = null;
    };
  }, []);

  const photos = allSanityTypeItems(data, "Photo");

  return (
    <>
      <Layout>
        <main className="main container-fluid p-0">
          {photos?.length > 0 ? (
            <div id="gallery" ref={ref} className={ `gallery ${loaded ? "show" : ""} pt-3 pb-5 pswp-gallery` }>
              {photos.map((photo) => (
                <PhotoCard key={photo._id} photo={photo} />
              ))}
            </div>
          ) : (
            <h2>...work in progress</h2>
          )}
        </main>
      </Layout>
    </>
  );
};

export const query = graphql`
  query {
    allSanityPhoto(sort: { orderRank: ASC }) {
      edges {
        node {
          _id
          title
          videoCover {
            _key
            asset {
              url
            }
          }
          cover {
            asset {
              height
              width
              url
              gatsbyImageData(width: 340, placeholder: BLURRED)
            }
          }
          gallery {
            _key
            asset {
              url
              width
              height
            }
          }
        }
      }
    }
  }
`;

export default PhotographyPage;

export const Head = () => {
  return (
    <>
      <Seo />
      <body className="home" />
    </>
  );
};
